export default "/* The actual timeline (the vertical ruler) */\r\n.timeline {\r\n  position: relative;\r\n  margin: 0;\r\n}\r\n/* The actual timeline (the vertical ruler) */\r\n.timeline::after {\r\n  content: '';\r\n  position: absolute;\r\n  width: 6px;\r\n  background-color: #2196F3;\r\n  top: 0;\r\n  bottom: 0;\r\n  left: 50%;\r\n  margin-left: -3px;\r\n}\r\n/* Container around content */\r\n.container {\r\n  margin: 0;\r\n  position: relative;\r\n  background-color: inherit;\r\n  width: 50%;\r\n}\r\n/* The circles on the timeline */\r\n.container::after {\r\n  content: '';\r\n  position: absolute;\r\n  width: 16px;\r\n  height: 16px;\r\n  right: -8px;\r\n  background-color: #f2f2f2;\r\n  border: 4px solid #2196F3;\r\n  top: 25px;\r\n  border-radius: 50%;\r\n  z-index: 1;\r\n}\r\n/* Place the container to the left */\r\n.left {\r\n  left: 0;\r\n}\r\n/* Place the container to the right */\r\n.right {\r\n  left: 50%;\r\n}\r\n/* Add arrows to the left container (pointing right) */\r\n.left::before {\r\n  content: \" \";\r\n  height: 0;\r\n  position: absolute;\r\n  width: 0;\r\n  z-index: 1;\r\n  top:22px;\r\n  right: 10px;\r\n  border: medium solid white;\r\n  border-width: 10px 0 10px 10px;\r\n  border-color: transparent transparent transparent #2196F3;\r\n}\r\n/* Add arrows to the right container (pointing left) */\r\n.right::before {\r\n  content: \" \";\r\n  height: 0;\r\n  position: absolute;\r\n  top: 22px;\r\n  width: 0;\r\n  z-index: 1;\r\n  left: 10px;\r\n  border: medium solid white;\r\n  border-width: 10px 10px 10px 0;\r\n  border-color: transparent #2196F3 transparent transparent;\r\n}\r\n/* Fix the circle for containers on the right side */\r\n.right::after {\r\n  left: -8px;\r\n}\r\n/* The actual content */\r\n.content {\r\n  padding:4% 4%;\r\n  position: relative;\r\n  border-radius: 6px;\r\n}\r\n.contentAlign{\r\n  text-align: right;\r\n}\r\n.date{\r\n  color: #d60005;\r\n  font-style: italic;\r\n  font-weight: bold;\r\n}\r\n.ongoing{\r\n  color: #